import { DOMWidgetView } from '@jupyter-widgets/base';
import * as d3a from 'd3-array';
import { axisLeft } from 'd3-axis';
import { drag as d3drag } from 'd3-drag';
import * as d3s from 'd3-selection';
import * as d3sc from 'd3-scale';
import { schemeTableau10 } from 'd3-scale-chromatic';
import { line as d3line } from 'd3-shape';

const MARGIN = { top: 20, right: 15, bottom: 5, left: 30 };
const LEGEND_WIDTH = 50;

export class ParChords {
  private view: DOMWidgetView;
  private axisScale: d3sc.ScalePoint<string>;

  constructor(view: DOMWidgetView) {
    this.view = view;
    this.axisScale = d3sc.scalePoint().padding(0.1);

    const g = d3s
      .select(view.el)
      .append('svg')
      .attr('width', 100)
      .attr('height', 100)
      .append('g')
      .classed('substrate', true)
      .attr('transform', 'translate(' + MARGIN.left + ',' + MARGIN.top + ')');

    // add the color legend
    g.append('g')
      .attr('id', 'legend')
      .append('text')
      .classed('label', true)
      .style('text-anchor', 'end');

    // this.lensCursor = new LensCursor(view, g);

    this.updateSubstrateSize();
    this.view.model.on(
      'change:width change:height',
      () => this.updateSubstrateSize(),
      this.view
    );
    this.view.model.on(
      'change:_marks_val change:_marks_color',
      this.updateData,
      this
    );
  }

  private updateSubstrateSize() {
    const width = this.view.model.get('width') as number;
    const height = this.view.model.get('height') as number;

    d3s
      .select(this.view.el)
      .select('svg')
      .attr('width', width)
      .attr('height', height);

    this.updateData();
  }

  private updateData() {
    const fields = this.view.model.get('axis_fields') as string[];
    const values = this.view.model.get('_marks_val') as number[][];
    const cValues = this.view.model.get('_marks_color') as string[];

    const colorValues = d3a
      .rollups(
        cValues,
        (v) => v.length,
        (d) => d
      )
      .sort((a, b) => (a[1] < b[1] ? 1 : -1))
      .map((v) => v[0]);

    const colorScale = d3sc.scaleOrdinal(schemeTableau10).domain(colorValues);

    const substWidth =
      (this.view.model.get('width') as number) -
      MARGIN.left -
      (colorValues.length > 0 ? LEGEND_WIDTH : MARGIN.right);
    const substHeight =
      (this.view.model.get('height') as number) - MARGIN.top - MARGIN.bottom;

    // update the scale for x positions of axes
    this.axisScale.domain(fields).range([0, substWidth]);
    // init array of scales for y-position of points on parallel axes
    const valueScales = prepareValueScales(fields, values, [substHeight, 0]);

    const gSubstrate = d3s.select(this.view.el).select('g.substrate');

    // helper function that turns an item array to coordinate pairs
    const pathGenerator = d3line()
      .x((_v, vi) => this.axisScale(fields[vi]) as number)
      .y((v, vi) => valueScales[vi](v as unknown as number));

    // encode each item into a path
    gSubstrate
      .selectAll('path')
      .data(values)
      .join('path')
      .attr('d', pathGenerator as any)
      .style('fill', 'none')
      .style('stroke', (_d, i) => colorScale(cValues[i]))
      .style('opacity', 0.5);

    let dragOrigXPos = 0;
    const dragBehavior = d3drag<SVGTextElement, string>();
    dragBehavior
      .on('start', (event, datum) => {
        console.log('dragBehavior start: ' + datum);
        // n.b. d3.pointer is misleading here because DOM context changes from start to end
        dragOrigXPos = event.x;
      })
      // .on("drag", (event, d) => (d.x = event.x, d.y = event.y))
      .on('end', (event, datum) => {
        console.log('dragBehavior end: ' + datum);

        // console.log(event);
        const deltaX = event.x - dragOrigXPos;
        const steps = (deltaX / this.axisScale.step()) >> 0;
        // console.log('origX: ' + dragOrigXPos);
        // console.log('new X: ' + event.x);
        // console.log('delta: ' + deltaX);
        console.log('steps: ' + steps);

        const oldFields = this.view.model.get('axis_fields') as string[];
        const oldIndex = oldFields.indexOf(datum);
        if (oldIndex !== -1) {
          // n.b. we need to create a new array object
          const newFields = oldFields.filter((item) => item !== datum);
          // newFields.splice(oldIndex, 1);
          const newIndex = Math.max(
            0,
            Math.min(oldIndex + steps, newFields.length)
          );
          newFields.splice(newIndex, 0, datum);
          console.log(newFields);
          this.view.model.set('axis_fields', newFields);
          this.view.model.save_changes();
        }
      });

    // draw the parallel axes
    gSubstrate
      .selectAll('g.pcaxis')
      // for each field of the dataset -> add a 'g' element and have its name as key
      .data(fields, (d) => d as string)
      .join((enter) =>
        enter
          .append('g')
          .classed('pcaxis', true)
          // important to call 'text', so that g is returned by enter() function
          .call((enter) =>
            enter
              .append('text')
              .style('text-anchor', 'middle')
              .attr('y', -9)
              // .attr('draggable', true)
              .text((d) => d)
              .style('fill', 'black')
              .on('click', (_evt, datum) => {
                console.log('axis click: ' + datum);
                this.view.send({
                  event: 'axis_click',
                  field: datum,
                });
              })
              .call(dragBehavior)
          )
      )
      // on enter and update -> translate the g element to its horizontal position
      .attr('transform', (d) => 'translate(' + this.axisScale(d) + ')')
      .each(function (d, i) {
        d3s.select(this).call(axisLeft(valueScales[i]) as any);
      });

    // update the legend
    const gLegend = gSubstrate.select('g#legend');
    gLegend.attr(
      'transform',
      'translate(' + (substWidth + LEGEND_WIDTH) + ', -2)'
    );
    gLegend.select('text.label').text(this.view.model.get('color_field'));

    gLegend
      .selectAll('text.axis')
      .data(colorValues)
      .join('text')
      .attr('class', 'axis')
      .text((d: string) => d)
      .style('text-anchor', 'end')
      .attr('x', -9)
      .attr('y', (d, i) => i * 14 + 14);

    gLegend
      .selectAll('rect')
      .data(colorValues)
      .join('rect')
      .attr('x', -7)
      .attr('y', (d, i) => i * 14 + 10)
      .attr('width', 7)
      .attr('height', 2)
      .style('fill', (d: string) => colorScale(d));
  }
}

function prepareValueScales(
  fields: string[],
  values: number[][],
  range: Iterable<number>
): d3sc.ScaleLinear<number, number, never>[] {
  return fields.map((_f, fi) => {
    const vMin = d3a.min(values, (v) => v[fi]) || 0;
    const vMax = d3a.max(values, (v) => v[fi]) || 0;
    // console.log(_f + ' ' + vMin + ' ' + vMax);
    // console.log(values.map((v) => v[fi]));
    // vMax = vMax === vMin ? vMax + 1 : vMax;
    return d3sc.scaleLinear().range(range).domain([vMin, vMax]);
  });
}
